<template>
   <div>
      <!--  -->
      <loading :active.sync="isLoading"></loading>

      <div class="text-right my-4">
         <button class="btn btn-minor" @click="openModal(true)"></button>
      </div>
      <table class="table table-hover mb-5 backProducts_table">
         <thead>
            <tr class="">
               <th class="text-center" width=""></th>
               <th class="text-center"></th>
               <th class="text-center" width=""></th>
               <th class="text-center" width=""></th>
               <th class="text-center RWD_view" width="100"></th>
               <th class="text-center" width="120"></th>
            </tr>
         </thead>
         <tbody>
            <tr class="" v-for="(item) in products" :key="item.id">
               <td class="text-center">{{ item.category }}</td>
               <td class="text-center">{{ item.title }}</td>
               <td class="text-right">
                  {{ item.origin_price | currency }}
               </td>
               <td class="text-right">
                  {{ item.price | currency }}
               </td>
               <td class="text-center RWD_view">
                  <span v-if="item.is_enabled" class="text-success"></span>
                  <span v-else></span>
               </td>
               <td class="text-center">
                  <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
                  <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)"></button>
               </td>
            </tr>
         </tbody>
      </table>

      <!--  -->
      <page-tool :sendPage="pagination" @changePage="getProducts"></page-tool>

      <!-- <nav aria-label="Page navigation example">
         <ul class="pagination">
            <li class="page-item" :class="{'disabled': !pagination.has_pre}">
               <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page - 1)">
                  <span aria-hidden="true">&laquo;</span>
               </a>
            </li>

            <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active':pagination.current_page === page}">
               <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a>
            </li>
            
            <li class="page-item" :class="{'disabled': !pagination.has_next}">
               <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page + 1)">
                  <span aria-hidden="true">&raquo;</span>
               </a>
            </li>
         </ul>
      </nav> -->

      <!--  Modal -->
      <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
         <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
               <div class="modal-header">
                  <h5 class="modal-title" id="exampleModalLabel">
                     <span></span>
                  </h5>
                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                     <span aria-hidden="true">x</span>
                  </button>
               </div>
               <div class="modal-body w-100">
                  <div class="row">
                     <div class="col-sm-4">
                        <div class="form-group">
                           <label for="image"></label>
                           <input type="text" class="form-control" id="image" v-model="tempProduct.imageUrl"
                           placeholder="">
                        </div>
                        <div class="form-group">
                           <label for="customFile"> 
                              <i class="fas fa-spinner fa-spin" v-if="status"></i>
                           </label>
                           <input type="file" id="customFile" class="form-control"
                           ref="files" @change="uploadFile">
                        </div>
                        <img class="img-fluid" :src="tempProduct.imageUrl">
                     </div>
                     <div class="col-sm-8">
                        <div class="form-group">
                           <label for="title"></label>
                           <input type="text" class="form-control" id="title"
                           v-model="tempProduct.title" placeholder="">
                        </div>

                        <div class="form-row">
                           <div class="form-group col-md-6">
                              <label for="category"></label>
                              <input type="text" class="form-control" id="category"
                                 v-model="tempProduct.category" placeholder="">
                           </div>
                           <div class="form-group col-md-6">
                              <label for="price"></label>
                              <input type="unit" class="form-control" id="unit" v-model="tempProduct.unit" placeholder="">
                           </div>
                        </div>

                        <div class="form-row">
                           <div class="form-group col-md-6">
                              <label for="origin_price"></label>
                              <input type="number" class="form-control" id="origin_price" v-model="tempProduct.origin_price" placeholder="">
                           </div>
                           <div class="form-group col-md-6">
                              <label for="price"></label>
                              <input type="number" class="form-control" id="price" v-model="tempProduct.price" placeholder="">
                           </div>
                        </div>
                        <hr>
                        <div class="form-group">
                           <label for="description"></label>
                           <textarea type="text" class="form-control" id="description" v-model="tempProduct.description" placeholder=""></textarea>
                        </div>
                        <div class="form-group">
                           <label for="content"></label>
                           <textarea type="text" class="form-control" id="content" v-model="tempProduct.content" placeholder=""></textarea>
                        </div>
                        <div class="form-group">
                           <div class="form-check">
                              <!--  -->
                              <input class="form-check-input" type="checkbox"
                                 v-model="tempProduct.is_enabled" :true-value="1" :false-value="0" id="is_enabled">
                              <label class="form-check-label" for="is_enabled">
                                 
                              </label>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
               <div class="modal-footer">
                  <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                  <button type="button" class="btn btn-primary" @click="updateProduct"></button>
               </div>
            </div>
         </div>
      </div>

      <!--  Modal -->
      <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
         <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
               <div class="modal-header bg-danger text-white">
                  <h5 class="modal-title" id="exampleModalLabel">
                     <span></span>
                  </h5>
                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                     <span aria-hidden="true">&times;</span>
                  </button>
               </div>
               <div class="modal-body">
                   <strong class="text-danger">{{ tempProduct.title }}</strong> ()
               </div>
               <div class="modal-footer">
                  <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                  <button type="button" class="btn btn-danger"
                  @click="delProduct"></button>
               </div>
            </div>
         </div>
      </div>

   </div>
</template>

<script>
import $ from 'jquery';
import pageTool from './Pagination';

export default {
   data() {
      return {
         products: [], // 
         // tempProduct  modal  post 
         tempProduct: {},
         pagination: {},
         isNew: false,
         isLoading: false,
         // 
         status: false,
      }
   },
   components: {
      pageTool,
   },
   methods: {
      // page = 1   ES6 
      getProducts(page = 1) {
         const vm = this;
         const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM}/admin/products?page=${page}`;
         vm.isLoading = true;
         //  get 
         this.$http.get(api).then(response => {
            //console.log('1',response.data.products);
            vm.isLoading = false;
            vm.products = response.data.products;
            vm.pagination = response.data.pagination;
         });
      },
      openModal(isNew, item) {
         $('#productModal').modal('show');

         // if(isNew) 
         if(isNew) {
            this.tempProduct = {};
            this.isNew = true;
         } else {
            //  Object.assign() item 
            this.tempProduct = Object.assign({}, item);
            this.isNew = false;
         }
      },


      /*
         updateProduct() ", "

         1. [#main.js]  axios.defaults.withCredentials = true;

         2. [#Header.vue]  API  `${process.env.VUE_APP_API}/admin/signin`;

         3.  Chrome cookie 

            * https://paper.dropbox.com/doc/Vue-API-28OrjdvBouPMjspZUM7h7

            *  SameSitehttps://docs.microsoft.com/zh-tw/azure/active-directory/develop/howto-handle-samesite-cookie-changes-chrome-browser?tabs=dotnet
         
         4.  chrome 
      */

      updateProduct() {
         let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM}/admin/product`;
         let httpMethod = 'post';
         const vm = this;

         //  isNew  openModal 
         if(!vm.isNew) {
            api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM}/admin/product/${vm.tempProduct.id}`;
            httpMethod = 'put';
         }
         this.$http[httpMethod](api, {data: vm.tempProduct}).then(response => {
            console.log(response.data);
            if(response.data.success){
               $('#productModal').modal('hide');
               vm.getProducts();
            } else {
               $('#productModal').modal('hide');
               vm.getProducts();
               console.log("");
            }
         });
      },

      openDelProductModal(item) {
         const vm = this;
         $('#delProductModal').modal('show');
         vm.tempProduct = Object.assign({}, item);
      },
      delProduct() {
         const vm = this;
         const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM}/admin/product/${vm.tempProduct.id}`;

         this.$http.delete(url).then((response) => {
            console.log('delProduct', vm.tempProduct);
            $('#delProductModal').modal('hide');
            //vm.isLoading = false;
            this.getProducts();
         });
      },

      // formData  ajax 
      uploadFile() {
         console.log(this);
         const vm = this;
         const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOM}/admin/upload`;
         const uploadedFile = vm.$refs.files.files[0];
         const formData = new FormData();
         formData.append('file-to-upload', uploadedFile);

         vm.status = true;
         this.$http.post(url, formData, {
            headers: {
               'Content-Type': "multipart/form-data",
            }
         }).then((response) => {
            //console.log(response.data);
            vm.status = false;
            if(response.data.success) {
               // vm.tempProduct.imageUrl = response.data.imageUrl;
               vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
            } else {
               this.$bus.$emit('messsage:push', response.data.message, 'danger');
            }
         });
      }
   },
   created() {
      const vm = this;
      vm.getProducts();
   },
}
</script>